<template>
	<div id="paotui-recharge">
		<public-header title="预存金充值"></public-header>
		<div class="content font-14">
			<div class="diy-price">
				<div>
					<div>余额({{Lang.dollarSignCn}})</div>
					<div class="margin-10-t font-bold font-16 c-danger">{{store.yucunjin}}</div>
				</div>
				<div class="border-1px-b padding-10-b" v-if="recharge.diy_status == 1 && select == 'other'">
					<input v-model.number="input" type="text" :placeholder="recharge.diy_min > 0 ? `最低${recharge.diy_min}${Lang.dollarSignCn}` : '输入金额'">
					<span class="c-danger font-bold">{{Lang.dollarSignCn}}</span>
				</div>
			</div>
			<van-row class="recharge-list">
				<van-col span="8" :class="{'recharge-item': 1, active: select == index}" v-for="(item, index) in recharge.items" :key="index">
					<div class="inner" @click="changeSelect(index, item.charge)">
						<div class="flex">
							<span class="font-bold font-16">{{item.charge}}</span>
							<span class="margin-5-l font-bold">{{Lang.dollarSignCn}}</span>
						</div>
						<div class="font-12 font-bold margin-5-t">
							赠送{{item.back}}{{Lang.dollarSignCn}}
						</div>
					</div>
				</van-col>
				<van-col span="8" :class="{'recharge-item': 1, active: select == 'other'}" v-if="recharge.diy_status == 1">
					<div class="inner" @click="changeSelect('other', input)">
						<div class="font-bold">其他金额</div>
					</div>
				</van-col>
			</van-row>
			<div class="padding-15">
				<van-button size="large" type="danger" :disabled="!islegal" @click="onSubmit">去充值</van-button>
			</div>
			<div class="agreement-tip">
				点击去充值，即已同意 <span @click="onTogglePopup">《充值协议》</span><br>
				余额不可提现、转赠、退还
			</div>
		</div>
		<agreement :show="popup" title="充值协议" :content="agreement" @agreementHide="onTogglePopup"></agreement>
		<bind-mobile :bind-mobile="bind_mobile"></bind-mobile>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import PublicFooter from '@/components/footer'
	import PublicHeader from '@/components/header'
	import BindMobile from '@/components/bindMobile'
	import Agreement from '@/components/agreement'
	export default {
		data() {
			return {
				select: 0,
				price: 0,
				input: '',
				recharge: {
					status: 0,
					diy_status: 1,
					diy_min: 0,
					back_type: 'credit2',
					items: []
				},
				store: {
					yucunjin: 0
				},
				islegal: false,
				showPreLoading: true,
				bind_mobile: {},
				agreement: '',
				popup: false
			}
		},
		components: {
			PublicHeader,
			PublicFooter,
			BindMobile,
			Agreement
		},
		watch: {
			input() {
				if(this.select == 'other') {
					this.price = this.input;
				}
			}
		},

		methods: {
			onLoad() {
				this.util.request({
					url: 'errander/recharge/index',
					data: {
					}
				}).then((res) => {
					this.showPreLoading = false;
					let result = res.data.message;
					if(result.errno) {
						this.$toast(result.message);
						return;
					}
					result = result.message;
					this.islegal = true;
					this.recharge = this.util.extend(this.recharge, result.recharge);
					if(this.recharge.items && this.recharge.items.length > 0) {
						this.price = this.recharge.items[0].charge;
					}
					this.store = this.util.extend(this.store, result.store)
					this.agreement = result.agreement;
					if(0 && window.bind_mobile) {
						this.bind_mobile = window.bind_mobile;
					}
				})
			},

			onSubmit() {
				if(!this.islegal) {
					return false;
				}
				if(this.recharge.diy_status == 1 && this.recharge.diy_min > 0 && this.price < this.recharge.diy_min) {
					this.util.$toast('最低充值' + this.recharge.diy_min + this.Lang.dollarSignCn);
					return false;
				}
				this.islegal = false;
				this.util.request({
					url: 'errander/recharge/submit',
					data: {
						price: this.price
					}
				}).then((res) => {
					let result = res.data.message;
					if(result.errno) {
						this.util.$toast(result.message);
						this.islegal = true;
						return false;
					}
					var orderId = result.message;
					this.$router.replace(this.util.getUrl({path: '/pages/public/pay', query: {order_id: orderId, order_type: 'errander_recharge'}}));
				})
			},

			changeSelect(index, charge) {
				this.select = index;
				this.price = charge;
			},

			onTogglePopup() {
				this.popup = !this.popup;
			}
		},
		mounted() {
			this.onLoad();
		}
	}
</script>

<style>
	#paotui-recharge .diy-price{
		display: flex;
		align-items: center;
		justify-content: space-between;
		padding: 15px;
		background-color: #fff;
	}
	#paotui-recharge .diy-price input{
		width: 90px;
	}
	#paotui-recharge .recharge-list{
		background-color: #fff;
		padding: 5px;
	}
	#paotui-recharge .recharge-item{
		padding: 5px;
	}
	#paotui-recharge .recharge-item .inner{
		position: relative;
		display: flex;
		flex-flow: column nowrap;
		align-items: center;
		justify-content: center;
		height: 70px;
		border-radius: 5px;
		border: 1px solid #e0e0e0;
		overflow: hidden;
	}
	#paotui-recharge .recharge-item.active .inner{
		color: #ff2d4b;
		border-color: #ff2d4b;
	}
	#paotui-recharge .recharge-item.active .inner:after{
		content: '';
		width: 31px;
		height: 24px;
		position: absolute;
		bottom: 0;
		right: 0;
		background: url('../../../../../static/img/selected-success.png') no-repeat right bottom;
		background-size: contain;
	}
	#paotui-recharge .agreement-tip{
		position: absolute;
		bottom: 15px;
		left: 0;
		right: 0;
		font-size: 12px;
		color: #999;
		text-align: center;
		line-height: 1.5;
	}
	#paotui-recharge .agreement-tip span{
		color: #ff2d4b;
		text-decoration: underline;
	}
</style>



